.sell-hero {
  .buying-hero-content {
    border-radius: 20px;
    background: var(--blue) url("../../img/sell-bg.webp") center / cover
      no-repeat;
    padding: 45px 20px;
  }
  .buying-hero-content-inner {
    max-width: 700px;
    margin: 0 auto;
  }
}

.media {
  margin-bottom: 100px;
  &__title {
    margin-bottom: 40px;
  }

  &__inner {
    display: grid;
    grid-template-columns: 1fr 490px;
    gap: 20px;
  }

  .media__left {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }

  &__cart {
    border-radius: clamp(10px, 2vw, 20px);
    background: #f8f8f8;
    display: flex;
    flex-direction: column;
    padding: 40px 25px;
    position: relative;
    overflow: hidden;
    &:nth-child(1) {
      grid-column: span 2;
      &:after {
        content: "";
        position: absolute;
        bottom: 0;
        right: 0;
        width: 420px;
        height: 251px;
        background: url("../../img/sell-page/cart-1.webp") no-repeat;
      }
      img {
        max-width: 215px;
        display: block;
        margin-top: 40px;
      }
    }

    &:nth-child(2) {
      background: #fdffdd;
    }

    &:nth-child(3) {
      padding-bottom: 0;
      background: var(--blue);
      .media__cart-title {
        color: var(--accent);
      }
      .media__cart-desc {
        color: var(--accent);
      }
      img {
        margin-top: auto;
        text-align: center;
      }
    }

    &-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: clamp(22px, 3vw, 30px);
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
      margin-bottom: 12px;
    }
    &-desc {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 16px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
      margin-bottom: 10px;
      flex: 1 1 auto;
    }
  }

  .last-cart {
    background: var(--blue);
    height: 100%;

    .media__cart-title {
      color: var(--accent);
    }
    .media__cart-desc {
      color: var(--accent);
    }
    img {
      max-width: 100%;
    }
    &:after {
      display: none;
    }
  }
}

.tariff {
  margin-bottom: 100px;

  &__title {
    margin-bottom: 40px;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }

  &__item {
    background: #f8f8f8;
    border-radius: clamp(10px, 2vw, 20px);
    padding: 30px 20px;
    &:last-child {
      background: var(--blue);
    }
    &-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: clamp(22px, 3vw, 30px);
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--accent);
      margin-bottom: 32px;
    }

    ul {
      display: flex;
      flex-direction: column;
      gap: 20px;

      li {
        position: relative;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: clamp(14px, 3vw, 16px);
        line-height: 128%;
        letter-spacing: -0.01em;
        color: var(--accent);
        padding-left: 32px;
        &:before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 23px;
          height: 23px;
          background: url("../../img/icons/checked.svg") no-repeat;
        }

        &.disabled {
          opacity: 0.5;

          &:before {
            background: url("../../img/icons/close.svg") no-repeat;
          }
        }
      }
    }

    .btn {
      background: var(--accent);
      color: var(--white);
      padding-block: 20px;
      margin-top: 50px;
      width: 100%;
      @media (any-hover: hover) {
        &:hover {
          background: var(--hovered);
        }
      }
    }
  }
}

.steps-work {
  margin-bottom: 100px;
  &__title {
    margin-bottom: 70px;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    row-gap: 60px;
  }

  &__item {
    position: relative;
    border-radius: clamp(10px, 2vw, 20px);
    padding: 30px 25px;
    display: flex;
    flex-direction: column;
    background: #f8f8f8;
    &-icon {
      border-radius: 5px;
      width: 48px;
      height: 48px;
      display: grid;
      place-items: center;
      background: var(--accent);
      position: absolute;
      top: -30px;
      left: 20px;
    }

    .title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 25px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--accent);
      margin-bottom: 13px;
    }
    .desc {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 16px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: #272727;
      flex: 1 1 auto;
      margin-bottom: 90px;
    }
    .progress {
      display: flex;
      flex-direction: column;
      .number {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: #272727;
        margin-bottom: 7px;
      }

      .progress-line {
        width: 100%;
        height: 2px;
        border-radius: 2px;
        background: #ababab;
        position: relative;

        &:before {
          content: "";
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          width: 20%;
          height: 2px;
          background: var(--accent);
        }
      }
    }

    &:nth-child(2) {
      .progress-line {
        &:before {
          width: 30%;
        }
      }
    }
    &:nth-child(3) {
      .progress-line {
        &:before {
          width: 40%;
        }
      }
    }
    &:nth-child(4) {
      .progress-line {
        &:before {
          width: 50%;
        }
      }
    }
    &:nth-child(5) {
      .progress-line {
        &:before {
          width: 60%;
        }
      }
    }
    &:nth-child(6) {
      .progress-line {
        &:before {
          width: 100%;
        }
      }
    }
  }
}
